.game-container {
  display: flex;
  height: 100vh;
  background-color: #1a1a1a;
  color: #fff;
}

.game-left {
  flex: 3;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-right: 1px solid #333;
  height: 100%;
  overflow: hidden;
}

.game-right {
  flex: 2;
  padding: 20px;
  display: flex;
  flex-direction: column;
  min-width: 400px;
  max-width: 800px;
} 